:root{
  --section-overlay:rgba(0,0,0,0.2);
  --background-overlay:rgba(0,0,0,0.6);
  --forecast-overlay:rgba(255,255,255,0.7);
}
*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.main{
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
}
.main .overlay{
  width: 100%;
  height: 100vh;
  background-color: var(--section-overlay);
}

.main .overlay .container{
  max-width: 800px;
  margin: auto;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
  /* background-color: blue; */
}

.main .overlay .container .section
{
  width: 100%;
  padding: 1rem;
  border-radius: 8px;
  color: white;
}

.main .overlay .container .section_input{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color:var(--background-overlay);
}

.main .overlay .container .section_input >.input{
  border: 0.2px solid white;
  border-radius: 0.4rem;
  background-color: transparent;
  color: white;
  font-size: 20px;
  padding: 0.5rem;

}
.main .overlay .container .section_input >button{
  padding: 10px 50px;
  font-size: 20px;
  border-radius: 0.4rem;
  border: none;
  font-weight: 900;
  background-color: white;
}
.main .overlay .container .section_input button:hover{
  cursor: pointer;
  background-color: lightgray;
}

.main .overlay .maincontainer{
  width: 1400px;
  margin: auto;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 1rem;
  /* background-color: blue; */
}
.main .overlay .maincontainer .forcast{
  width:700px;
  height:100%;
  background-color:var(--background-overlay);
  padding: 1rem;
  border-radius: 20px;
  display: flex;
  flex-direction:row ;
  flex-wrap: wrap;
}

.main .overlay .maincontainer .weather{
  width:400px;
  height: 100%;
  background-color:var(--background-overlay);
  padding: 1rem;
  border-radius: 20px;
  
}

.main .overlay .maincontainer .forcast>h1{
  color: white;
  margin: 1rem;
  margin-bottom: 25px;
  border-bottom: 1px solid white;


}

.main .overlay .maincontainer .forcast .fmain{
  width:110px;
  height: 110px;
  background-color:var(--forecast-overlay);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-right: 1.4rem;
  margin-bottom: 1.4rem;
  border-radius: 0.5rem;
}

.main .overlay .maincontainer .forcast .title
{
  width: 100%;
  margin-bottom:20px;
  /* background-color: blue; */
  height: 50px;
  position: sticky;
  top: 0;
  color: white;
  border-bottom:1px;
}
.main .overlay .maincontainer .forcast .title>h1{
  border-bottom: 1px solid white;
}

.main .overlay .maincontainer .forcast .fmain>h3{
  /* font-size: small; */
  margin-left: 25px;
  margin-right: 25px;
}

.main .overlay .maincontainer .forcast .mainforecast{
  height:26rem;
  /* background-color: blue; */
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow-x: scroll;
}
.main .overlay .maincontainer .forcast .mainforecast .Error{
  color:white;
  font-size: xx-large;
  font-weight: bold;
  text-align: center;
  top: 35%;
  position: relative;
}

.main .overlay .maincontainer .forcast .mainforecast .load{
  position: relative;
  left: 35%;
  top: 20%;
  /* width: 100px; */
  /* size: 50px; */
}

.main .overlay .maincontainer .forcast .mainforecast::-webkit-scrollbar{
  display: none;
}
.main .overlay .maincontainer .weather{
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.main .overlay .maincontainer .weather .temp
{
  width:18rem;
  height: 7rem;
  /* background-color: blue; */
  display: flex;
  justify-content: space-between;
}
.main .overlay .maincontainer .weather .temp>h1{
  position: relative;
  top: 15px;
  color: white;
  /* border-bottom: 1px solid white; */
}
.main .overlay .maincontainer .weather .wimg{
  height:6.5rem;
  width: 6.5rem;
  background-color: var(--forecast-overlay);
  display: flex;
  justify-content: center;
  /* align-items: center; */
  border-radius: 0.7rem;
  
}
.main .overlay .maincontainer .weather .wimg .iconimage{
  size: 100px;
}

.main .overlay .maincontainer .weather .location{
  color: white;
  font-size: large;
  display: flex;
  flex-direction:row;
  align-items: center;
  width: 100%;
  justify-content:center;
}

.main .overlay .maincontainer .weather .location>span{
  color:white;
  /* margin: 15px; */
  margin-right: 10px;
  font-weight: bolder;
  display: flex;
  justify-content: space-around;

}

.main .overlay .maincontainer .weather .location>span p{
  border-bottom: 1px solid white;
}
.main .overlay .maincontainer .weather .location>span #first{
  margin-left: 10px;
}
.main .overlay .maincontainer .weather .dateTime>h5{
  color: white;
  
}
.main .overlay .maincontainer .weather .dateTime>#date
{
  margin-right: 60px;
}

.main .overlay .maincontainer .weather .dateTime{
  display: flex;
  align-items: center;
  /* width: 100%; */
  justify-content:center;
  font-size: large;
}

.main .overlay .maincontainer .weather  .extra{
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.main .overlay .maincontainer .weather  .extra .wind{
  height: 4.5rem;
  width: 10rem;
  background-color:blue;
  margin-top: 30px;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main .overlay .maincontainer .weather  .extra .wind>#win{
  font-size: small;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin-top: 5px; */
}
.main .overlay .maincontainer .weather  .extra .wind>#hava{
  font-size: smaller;
  font-weight: bold;
  text-align: center;

}
.main .overlay .maincontainer .weather  .extra .wind>#dir{
  font-size: smaller;
  font-weight: bold;
  text-align: center;
  /* margin-top: 10px; */
}

.main .overlay .maincontainer .weather  .extra .wind>p>span{
  margin-left: 10px;

}
.main .overlay .maincontainer .weather .hum{
  height: 4.5rem;
  width: 10rem;
  background-color: rgb(13, 153, 13);
  margin-top: 30px;
  margin-left: 40px;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main .overlay .maincontainer .weather .hum>#humi{
  font-size: small;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main .overlay .maincontainer .weather .hum #h1{
  font-size: smaller;
  font-weight: bold;
  text-align: center;

}
.main .overlay .maincontainer .weather .condition #con{
  margin-top: 40px;
  font-size: large;
  color: white;
  font-weight: bold;
  border-bottom: 1px solid white;
}

.main .overlay .maincontainer .weather .load{
  top:35%;
  position: relative;
}

.main .overlay .maincontainer .weather .Error
{
  color: white;
  font-size: xx-large;
  font-weight: bold;
  top: 45%;
  position: relative;
}

 /* Responsive */


 @media (max-width:420px) {
  .main .overlay .container .section_input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 350px;
    background-color:var(--background-overlay);
  }
  .main .overlay .container .section_input>.input{
    border: 0.2px solid white;
    border-radius: 0.4rem;
    width: 200px;
    background-color: transparent;
    color: white;
    font-size: 20px;
    padding: 0.5rem;
  
  }
  .main .overlay .container .section_input>button{
    /* padding: 10px 50px; */
    font-size: 20px;
    border-radius: 0.4rem;
    border:none;
    width: 10px;
    font-weight: 900;
    background-color: white;
  }
  .main{
    width: 415px;
    height: 915px;
    background-position: center;
    background-size: cover;
  }
  .main .overlay{
    width: 415px;
    height: 915px;
    background-color: var(--section-overlay);
  }
  .main .overlay .maincontainer{
    width: 400px;
    /* margin-left:10px; */
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    /* background-color: blue; */
  }
  .main .overlay .maincontainer .forcast{
    width:380px;
    height:200px;
    background-color:var(--background-overlay);
    border-radius: 20px;
    display: flex;
    flex-direction:row;
    margin-bottom: 30px;
    top: -60px;  
    position: relative;
  }
  .main .overlay .maincontainer .forcast .title>h1{
    color: white;
    /* margin: 1rem; */
    /* margin-bottom: 25px; */
    border-bottom: 1px solid white;
    font-size: 25px;
  
  
  }
  .main .overlay .maincontainer .forcast .title{
    /* top: -5px; */
    height: 20px;
  }
  .main .overlay .maincontainer .forcast .mainforecast{
    height:130px;
    width: 100%;
    display: flex;
    top: -5px;
    flex-direction: column;
    /* flex-wrap: wrap; */
    overflow-x: scroll;
  }
  .main .overlay .maincontainer .forcast .fmain{
    width:110px;
    height: 110px;
    background-color:var(--forecast-overlay);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-right: 1.4rem;
    /* margin-bottom: 1.4rem; */
    border-radius: 0.5rem;
  }

  .main .overlay .maincontainer .weather{
    width:400px;
    height: 100%;
    background-color:var(--background-overlay);
    padding: 1rem;
    border-radius: 20px;
    position: relative;
    top: -40px;
  }
  

  
 }